import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';
import { Observable } from "rxjs/Observable";
import { AngularFireDatabase } from "angularfire2/database";
import { IDataProvider } from "../i-data/i-data";
import { FirebaseAuthProvider } from "../firebase-auth/firebase-auth";
import { Observer } from "rxjs/Observer";

/**
 * @author Matthias
 * Implementierung der IDataProvider-Schnittstelle fuer die Firebase-Datenbank.
 * Mit diesem Provider ist es moeglichen Daten aus dem Backend zu
 * - laden,
 * - speichern,
 * - erstellen
 * - und loeschen.
 */
@Injectable()
export class FirebaseDataProvider implements IDataProvider {

    private firebaseDataObservable: Observable<any>;
    private userData: object;

    private dataObservable: Observable<any>;
    private dataObserver: Observer<any>;

    constructor(private auth: FirebaseAuthProvider,
                private db: AngularFireDatabase) {

        this.initDataObservable();
        this.waitForLogin();
    }

    private initDataObservable(): void {
        this.dataObservable = new Observable<any>(observer => {
            this.dataObserver = observer;
        });
    }

    private waitForLogin(): void {

        this.auth.isLoggedInAsObservable().subscribe(result => {
            if (result) {
                console.log('load user data');
                this.loadUserData();
            }
        })

    }

    private loadUserData(): void {

        this.firebaseDataObservable = this.db.object('/' + this.auth.getUserId() + '/').valueChanges();
        this.firebaseDataObservable.subscribe(data => {

            if (data === null) {
                console.log('create user object');
                this.createUserDataObject();
            } else {
                this.dataObserver.next(data);
            }
        });
    }

    private createUserDataObject(): void {
        this.userData = {
            user: {
                email: '',
                name: ''
            },
            projects: [],
            times: []
        };

        this.db.object('/' + this.auth.getUserId() + '/').update(this.userData);
    }

    public get(itemName: string, id: string = null): Observable<any> {

        return this.dataObservable.map(value => this.keyMapping(value, itemName, id));
    }

    private keyMapping(userData: any, itemName: string, id: string): any {
        if (id != null) {

            let returnValue = null;
            let arrayObjects = <Array<any>>userData[itemName];

            arrayObjects.forEach((value => {
                if(value['id'] == id) {
                    returnValue = value;
                }
            }));

            return returnValue;

        } else {
            return userData[itemName];
        }
    }

    public insert(itemName: string, item: any): boolean {
        return null;
    }

    public update(itemName: string, item: any, id: string): boolean {
        return null;
    }

    public delete(itemName: string, id: string): boolean {
        return null;
    }
}



